body {
	margin: 0;
	padding: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	font-family: "Jost", sans-serif;
	background: linear-gradient(to bottom, #0f0c29, #302b63, #24243e);
}
.main {
	width: 350px;
	height: 500px;
	overflow: hidden;
	border-radius: 10px;
	box-shadow: 5px 20px 20px #000;
}
.signup {
	position: relative;
	width: 100%;
	height: 100%;
}

h1 {
	color: #fff;
	font-size: 2.3em;
	justify-content: center;
	text-align: center;
	display: flex;
	margin: 60px;
	font-weight: bold;
}

h2 {
	color: #fff;
	font-size: 2.3em;
	justify-content: center;
	text-align: center;
	display: flex;
	margin: 60px;
	font-weight: bold;
}
input {
	width: 60%;
	height: 20px;
	background: #e0dede;
	justify-content: center;
	display: flex;
	margin: 20px auto;
	padding: 10px;
	border: none;
	outline: none;
	border-radius: 5px;
}
button {
	width: 60%;
	height: 40px;
	margin: 10px auto;
	justify-content: center;
	display: block;
	color: #fff;
	background: #573b8a;
	font-size: 1em;
	font-weight: bold;
	margin-top: 20px;
	outline: none;
	border: none;
	border-radius: 5px;
	transition: 0.2s ease-in;
	cursor: pointer;
}
button:hover {
	background: #6d44b8;
}
